body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
 
}
body{
  background-color: #15202B;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.NavBarContainer{
    position: fixed;
    display: flex;
    justify-content:left;
    width: 80%;
    left: 10%;
    height: 58px;
    z-index: 1;
    background-color: #343A40;



}
.navButton{
color: #FFFFFF ;
background-color: transparent;
padding-left: 5%;

}

.profileContainer{
    position: absolute;
    top: 60px;
    display: flex;
    justify-content: center;
}
.profileTest{
    color: white;
}
.home-container{
position: absolute;
top: 70px;
align-self: center;
height: 95%;
width: 100vw;
background-color: #15202B;
flex-direction: column;
display: flex;    
align-items: center;
}
/* +++++form+++++ */
.create-tweet{
    position: relative;
    margin: auto;
    width: 600px;
    height: 180px;
    border: 3px solid black;
    padding: 10px;
    margin: 5px;
}
.tweet-form{

}

.tweet-textarea{
width: 564px;
height: 163px;
padding: 12px 20px;
box-sizing: border-box;
border-radius: 4px;
resize: none;
background-color: #15202B;
color: #CCCCCC;
border: none;
}
.tweet-button{
background-color:#007BFF;
position:absolute;
height: 34px;
width: 68px;
right:10px;
bottom: 20px;
color: #ffffff;
}

/* +++++List+++++ */
.tweet-list{
background-color: #15202B;
display: flex;
flex-direction:column-reverse;

}
.tweetCapError{
    color: #721C24;
    background-color: #F5C6CB;
    
}


.tweetCard{
    position: relative;
    color: #FFFFFF;
    background-color: rgb(146, 138, 138);
    width: 600px;
    height: 100px;
    border:2px black solid ;
    margin: 15px;
    background-color:#343A40;
}

.tweetName{
    position: absolute;
    left: 20px;
    top: 20px;
    margin: 0px;

}

.tweetTime{
    position: absolute;
    top:18px;
    right:20px;
    margin: 0px;
}

.tweetText{
    position: absolute;
    width: 455px;
    top: 48px;
    left: 20px;
    overflow: auto;
    margin: 0px;
}
